html, body, #root, .App{
	width:100%;
	height:100%;
	margin:0 auto;
	display:flex;
	overflow:auto;

	justify-content:center;
	align-items:center;

	background-color:#f2f2f2;
	user-select:none;
}

#drum-machine{
	display:grid;
	margin:0 auto;
	width:50%;
	height:40%;
	min-width:230px;
	min-height:;

	grid-template-columns:1fr 45%;
	padding:30px;
	position:relative;

	background-color:white;
	outline:5px solid #00e6e6;
}

#pads{
	display:grid;
	grid-template-columns:repeat(3, 1fr);
	grid-template-rows:repeat(3, 1fr);
	grid-gap:20px;
}

#info{
	display:grid;
	grid-template-rows:1fr 1fr
}

@media (max-width:1200px){


	#drum-machine{
		display:grid;
		min-height:410px;
		grid-template-rows:1fr 35%;
		grid-template-columns:1fr;
	}

	#pads{
		grid-gap:15px;
	}


}
